   max-width: 225px;
   max-height: 225px;
   margin: 10px;
} */

@-webkit-keyframes slideDown {
   from {
      margin-top: 10%;
   }

   to {
      margin-top: 20%;
   }
}

@keyframes slideDown {
   from {
      margin-top: 10%;
   }

   to {
      margin-top: 20%;
   }
}

@-webkit-keyframes slideUp {
   from {
      margin-top: 10%;
   }

   to {
      margin-top: 0%;
   }
}

@keyframes slideUp {
   from {
      margin-top: 10%;
   }

   to {
      margin-top: 0%;
   }
}

@-webkit-keyframes fadeIn {
   from {
      opacity: 0;
   }

   to {
      opacity: 1;
   }
}

@keyframes fadeIn {
   from {
      opacity: 0;
   }

   to {
      opacity: 1;
   }
}
